<template>
    <!-- <div class="start" :class="{end : 모달창보이게}"> 
        <Modal :모달창보이게="모달창보이게" :원룸들="원룸들" :아이디="아이디" @closeModal="모달창보이게 = false" />
    </div> -->
    <transition name="fade">
         <Modal :모달창보이게="모달창보이게" :원룸들="원룸들" :아이디="아이디" @closeModal="모달창보이게 = false" />
    </transition>
    <!-- <Discount v-if="showDiscount == true"/> -->
    <Discount />
    <nav>
        <ul class="tab">
            <li><a @click="step = 1" class="tab_home">1</a></li>
            <li><a @click="step = 2" class="tab_gift">2</a></li>
            <li><a @click="step = 3" class="tab_ai">3</a></li>
            <li><a @click="step = 3" class="tab_money">3</a></li>
            <li><a @click="step = 3" class="tab_bag">3</a></li>
        </ul>
    </nav>
    <div class="one_wrap" v-if="step == 1">
            <div class="search_wrap">
                <div class="search-container">
                    <button class="search-button"><span class="blind">검색</span></button>
                    <input type="text" class="search-input" placeholder="토스쇼핑 검색" @change="searchGroup($event)">
                </div>
                <div class="qr_pay">
                    <img src="./assets/icon_wallet.svg" alt=""/>
                </div>
            </div>
            <div class="slide_wrap">
                <div class="slide_box" :style="{marginLeft:slidemargin+'%'}">
                    <div class="slide"><img src="./assets/slide1.png" alt="" /></div>
                    <div class="slide" id="slide2"><img src="./assets/slide2.png" alt="" /></div>
                    <div class="slide"><img src="./assets/slide3.png" alt="" /></div>
                    <div class="slide"><img src="./assets/slide4.png" alt="" /></div>
                </div>
            </div>
            <div class="product_box_title">
                <span>하루특가</span>
                <span class="sort_title" @click="openSort">정렬하기</span>
                    <div class="sort_select" v-if="정렬 == true">
                        <ul>
                            <li><button @click="priceSort">낮은가격순</button></li>
                            <li><button @click="priceUpSort">높은가격순</button></li>
                            <li><button @click="stringUp">가나다순</button></li>
                            <li><button @click="stringDown">문자역순</button></li>
                            <li><button @click="sortBack">되돌리기</button></li>
                        </ul>
                    </div>
            </div>
            <div class="timesale_box" id="timesale">
                <span class="hours">{{ 시간 }}</span> : 
                <span class="minutes">{{ 분 }}</span> : 
                <span class="seconds">{{ 초 }}</span>
            </div>
            <div class="product_box" v-for="(p,i) in 상품" :key="i">
                <div class="img_box">
                    <div class="label_bestcost" v-if="상품[i].best == true">최저가보상</div>
                    <a href="#none" @click="모달창보이게=true">
                        <img :src="상품[i].image" alt="" />
                    </a>
                </div>
                <h4 class="product_title">{{ 상품[i].title }}</h4>
                <div class="price_box">
                    <div>
                        <span class="product_lowcost">{{상품[i].lowcost}}%</span><span class="product_price">{{ 상품[i].price }}원</span>
                        <button class="product_good" @click="increase(i)">
                            <span :id="'goodbtn'+상품[i].id">🩵</span></button>
                    </div>
                    <p class="product_goodnumber">{{상품[i].good}}명이 좋아요 함 &middot; 무료배송</p>
                </div>
            </div>
            <div id="no_result"><span>😅</span>결과가 없습니다</div>
            <div class="more_btn_wrap">
                <button @click="more" id="more_btn">더보기</button>
            </div>
            
    </div>

    <div v-if="step == 2">
        <button class="price_sort" @click="priceSort">가격낮은순</button>
        <button class="price_sort" @click="priceUpSort">가격내림차순</button>
        <button class="price_sort" @click="stringUp">가나다순정렬</button>
        <button class="price_sort" @click="stringDown">문자역순정렬</button>
        <button class="price_sort" @click="sortBack">되돌리기</button>
        <Card :모달창보이게="모달창보이게" :원룸들="원룸들" :아이디="아이디" @openModal="모달창보이게 = true ; 아이디=$event"/><!--자식이 보낸 데이터  $event에 담겨있음-->
        <!-- <Card :모달창보이게="모달창보이게" :원룸="원룸들[0]" :아이디="아이디" @openModal="모달창보이게 = true ; 아이디=$event"/> -->
    </div>
    <div v-if="step == 3">
        <h2>V모델 인풋값 입력</h2>
        <!--$event.target.value 입력값은 숫자로 입력해도 기본적으로 문자형으로 나옴-->
        <p>{{ month }} 개월 선택함</p>
        <select v-model="month" v-if="check==false">
            <option v-if="month == 0">{{ month }}</option>
            <option>12</option>
            <option>24</option>
            <option>36</option>
            <option>48</option>
        </select>
        <p v-else>개월수:<input @input="month = $event.target.value;" :value="month"/> </p>
        <p>직접입력 : <input type="checkbox" v-model="check">{{ check }}</p>
        <p>총 월세 : {{ month * 300000}} 만원</p>
        <p id="alert"></p>
        <input type="range" min="0" max="12" v-model="rangeValue"><span>{{ rangeValue }}</span>

    </div>
</template>

<script>
import data from "./assets/oneroom.js";
import Product from "./assets/product.js";
import Discount from "./components/Discount.vue";
import Modal from "./components/Modal.vue";
import Card from "./components/Card.vue";
import axios from 'axios';

export default {
    name: "App",
    data() {
        return {
            showDiscount : true,
            price1: 80, //object 자료 형식
            product: ["역삼", "천호", "용산"],
            신고수: [0, 0, 0],
            모달창보이게: false,
            정렬:false,
            원룸들: data,
            원룸들오리지널:[...data],
            step: 1,
            아이디:0,
            month : 0,
            check : false,
            rangeValue : 0,
            slidemargin:0, //슬라이드이동
            상품:Product,
            상품오리지널:[...Product],
            좋아요누름:false,
            시간:23,
            분:59,
            초:59,
            누른횟수:3,
            
            
        };
    },
    watch : { //인풋에 필수적 데이터 변화할 때마다 코드를 실행해서 데이터 유효성 체크
        month(){
            if(isNaN(this.month)||this.month > 48){
                this.month = 0;
                document.getElementById('alert').innerText = '48개월 이하만 계약가능합니다 (숫자입력불가)';
            }
        },

    },
    methods: {
        searchGroup(event) {
            const len = this.상품.length;

            for (let i = 0; i < len; i++) {
                if (
                this.상품[i].title.includes(event.target.value) === false 
                // &&this.상품[i].debut.includes(event.target.value) === false 
                // &&this.상품[i].member.includes(event.target.value) === false
                ) {
                // document.querySelectorAll(".product_box")[i].style.display = "none";
                document.querySelectorAll(".product_box")[i].classList.remove('show');
                document.querySelectorAll(".product_box")[i].classList.add('hide');
                
                } else {
                // document.querySelectorAll(".product_box")[i].style.display = "block";
                document.querySelectorAll(".product_box")[i].classList.add('show');
                document.querySelectorAll(".product_box")[i].classList.remove('hide');
                
                }
            }

            if(document.getElementsByClassName("show").length == 0){
                   document.getElementById("no_result").style.display = "block";
                }else{
                    document.getElementById("no_result").style.display = "none";
                }


        },
        openSort(){
            if(this.정렬==false){
                this.정렬=true;
                
            }else{
                this.정렬=false;
            }
        },
        more(){
            axios.get('https://rahyeri.github.io/vue/more0.json')
            .then( result => {
                //누른횟수
                this.누른횟수++;
                this.상품.push(result.data[this.누른횟수]);
                console.log(result.data[this.누른횟수]);
                if(this.누른횟수 == 10){
                    document.getElementById('more_btn').style.display='none';
                }
            }).catch( ()=>{
                alert('얏호');
            });
        },
        increase(a) {
            if(this.좋아요누름==false){
                this.상품[a].good +=1;
                this.좋아요누름=true;
                document.getElementById('goodbtn' + this.상품[a].id).innerText = '💖';
                
            }else{
                this.상품[a].good -=1;
                this.좋아요누름=false;
                document.getElementById('goodbtn' + this.상품[a].id).innerText = '🩵';
            }
            
        },
        priceSort(){
            this.정렬=false;
            this.상품.sort(function(a,b){
                return a.price - b.price //음수면 a를 왼쪽으로
            });
            this.원룸들.sort(function(a,b){
                return a.price - b.price //음수면 a를 왼쪽으로
            });
        },
        priceUpSort(){
            this.정렬=false;
            this.상품.sort(function(a,b){
                return b.price - a.price 
            });
            this.원룸들.sort(function(a,b){
                return b.price - a.price 
            });
        },
        sortBack(){
            this.정렬=false;
            this.원룸들 = [...this.원룸들오리지널]; //오리지널의 복사본 보존을 원룸들에 집어넣기
            this.상품 = [...this.상품오리지널]; //오리지널의 복사본 보존을 원룸들에 집어넣기
        }
        ,
        stringDown(){
            this.정렬=false;
            this.원룸들.sort(function(a,b){
                if(a.title>b.title) return -1; 
                if(a.title<b.title) return 1; // 내림차순 정렬 
                return 0;
            });
            this.상품.sort(function(a,b){
                if(a.title>b.title) return -1; 
                if(a.title<b.title) return 1; // 내림차순 정렬 
                return 0;
            });
        },
        stringUp(){
            this.정렬=false;
            this.원룸들.sort(function(a,b){
                if(a.title<b.title) return -1; 
                if(a.title>b.title) return 1; // 오름차순 정렬 
                return 0;
            });
            this.상품.sort(function(a,b){
                if(a.title<b.title) return -1; 
                if(a.title>b.title) return 1; // 오름차순 정렬 
                return 0;
            });
        },
    },
    components: {
        Discount : Discount,
        Modal : Modal,
        Card : Card,
    },
    mounted(){
        setTimeout(()=>{
            this.showDiscount = false;
        },5000);
    },
    created(){
        setInterval(()=>{
        //    3초에 한번씩 슬라이드박스의 마진을 우우측으로 옮겨줘 
            this.slidemargin -= 100;
            
            if(this.slidemargin == -400){
                this.slidemargin=0;
            }
            
        },3000);
        setInterval(()=>{
            this.초--;
            if(this.초==0){
                this.초 = 59;
                this.분-=1;
                this.초 = '' +  this.초;
            }else if(this.초<10){
                this.초 = '0'+this.초;
            }
        },1000);
    }
};


</script>


<style>
@import url('./style.css');

</style>


